<template>
  <div class="navBar">
    <div class="left">
      <div class="icon" @click="back(-1)">
        <i class="iconfont icon-zuojiantou"></i>
      </div>
      <div class="icon" @click="back(1)">
        <i class="iconfont icon-youjiantou"></i>
      </div>

      <div class="search icon" @click="search">
        <van-search
          placeholder="搜索音乐"
          @search="onSearch"
          ref="searchRef"
          v-model="searchVal"
          clearable="true"
          @clear="onClear"
          @update:model-value="updateVal"
        />
        <div class="tips" v-if="show">
          <div class="t-left">
            <p class="title">热门搜索</p>
            <van-divider class="row1-line" />
            <van-divider class="row2-line" />
            <ul class="rank" v-if="tipList">
              <li
                v-for="item in tipList"
                :key="item.n"
                class="rank-item"
                @click="addVal(item.k)"
              >
                {{ item.k }}
                <span class="playNum">{{ item.n / 1000 }}万</span>
              </li>
            </ul>
          </div>
          <div class="t-right">
            <div class="title">
              <p>搜索历史</p>
              <p class="clear" @click.stop="clearHistory">清空</p>
            </div>
            <ul class="rank" v-if="historyList.length">
              <li
                v-for="item in historyList"
                :key="item"
                class="rank-item"
                @click="addVal(item)"
              >
                {{ String(item).trim() }}
              </li>
            </ul>
          </div>
        </div>
        <div class="tips2" v-if="searchVal && tips">
          <p>在线音乐</p>
          <van-divider />
          <ul
            style="margin-left: 60px; position: relative; margin-top: 20px"
            v-if="keyList.song.itemlist.length"
          >
            <span style="position: absolute; left: -57px">单曲</span>
            <li
              v-for="item in keyList.song.itemlist"
              :key="item.id"
              class="keyItem"
              @click="addVal(item.name)"
            >
              {{ item.name }}
            </li>
          </ul>
          <ul
            style="margin-left: 60px; position: relative; margin-top: 20px"
            v-if="keyList.singer.itemlist.length"
          >
            <span style="position: absolute; left: -57px">歌手</span>
            <li
              v-for="item in keyList.singer.itemlist"
              :key="item.id"
              class="keyItem"
              @click="addVal(item.name)"
            >
              <img :src="item.pic" alt="" class="avator-sm" />
              {{ item.name }}
            </li>
          </ul>
          <ul
            style="margin-left: 60px; position: relative; margin-top: 20px"
            v-if="keyList.album.itemlist.length"
          >
            <span style="position: absolute; left: -57px">专辑</span>
            <li
              v-for="item in keyList.album.itemlist"
              :key="item.id"
              class="keyItem"
              @click="addVal(item.name)"
            >
              <img :src="item.pic" alt="" class="avator-sm" />
              {{ item.name }}
            </li>
          </ul>
          <ul
            style="margin-left: 60px; position: relative; margin-top: 20px"
            v-if="keyList.mv.itemlist.length"
          >
            <span style="position: absolute; left: -57px">MV</span>
            <li
              v-for="item in keyList.mv.itemlist"
              :key="item.id"
              class="keyItem"
              @click="addVal(item.name)"
            >
              {{ item.name }}
            </li>
          </ul>
        </div>
      </div>

      <div class="icon">
        <i class="iconfont icon-shitingliebiao"></i>
      </div>
    </div>
    <div class="right" v-if="profile.avatarUrl">
      <van-popover
        v-model:show="showPopover"
        :actions="actions"
        @select="onSelect"
      >
        <template #reference>
          <div class="user-wrapper" style="display:flex;align-items: center;">
            <div class="avator">
              <img :src="profile.avatarUrl" />
            </div>
            <div class="login">
              {{ profile.nickname }}
            </div>
          </div>
        </template>
      </van-popover>
    </div>
    <div class="right" v-else>
      <div class="avator">
        <img
          src="https://s.cn.bing.net/th?id=OIP-C.TZzS5T3d_COKBudQtzzQIwHaE8&w=306&h=204&c=8&rs=1&qlt=90&o=6&dpr=1.5&pid=3.1&rm=2"
          alt=""
        />
      </div>
      <div class="login" @click="toLogin">
        点击登录
        <van-popup
          v-model:show="showLog"
          round
          position="bottom"
          :style="{ height: '30%' }"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, reactive, ref, toRefs, watch } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import { useStore } from "vuex";
import "@vant/touch-emulator";
export default {
  setup() {
    const router = useRouter();
    const state = reactive({
      tipList: [],
      show: false,
      tips: false,
      historyList: [],
      searchVal: "",
      keyList: {},
      profile: {},
    });
    const showLog = ref(false);
    const showPopover = ref(false);

    // 通过 actions 属性来定义菜单选项
    const actions = [ { text: "退出登录" }];
    const onSelect = (act) => {
      if(act.text=='用户详情'){
        axios({
          url:`/mini/user/detail?uid=${state.profile.userId}`
        }).then(res=>{
          console.log(res);
        })
        
      }else{
        axios({
          url:'/mini/logout'
        })
        store.commit('deleteProfiler')
      }
    };
    const addVal = (val) => {
      state.show = false;
      state.searchVal = val;

      onSearch();
    };
    const searchRef = ref(null);
    const back = (e) => {
      router.go(e);
    };
    const toLogin1 = () => {
      showLog.value = true;
    };
    const toLogin = () => {
      router.push({ path: "/login" });
    };
    const search = () => {
      if (state.searchVal) return;
      state.show = !state.show;
    };
    const onSearch = () => {
      store.commit("setHistoryList", state.searchVal);
      router.push({ name: "searchPage", query: { keywords: state.searchVal } });
      state.show = false;
    };
    const clearHistory = () => {
      store.commit("deleteHistoryList");
      state.historyList = store.state.historyList;
    };
    const updateVal = () => {
      if (!state.searchVal) {
        state.tips = false;
        return;
      }
      state.show = false;
      axios({
        url: `/api/getSmartbox?key=${state.searchVal}`,
      }).then((res) => {
        state.keyList = res.data.response.data;
        state.tips = true;
      });
    };
    const onClear = () => {
      state.searchVal = "";
    };
    const store = useStore();
    watch(
      () => store.state.profile,
      (newVal) => {
        // console.log(newVal);
        state.profile = newVal;
      }
    );
    onMounted(() => {
      axios({
        url: "/api/getHotkey",
      }).then((res) => {
        state.tipList = res.data.response.data.hotkey.slice(0, 10);
        // console.log(res.data.response.data.hotkey);
      });
      state.historyList = store.state.historyList;
      state.profile = store.state.profile;
    });
    return {
      clearHistory,
      toLogin,
      showLog,
      back,
      addVal,
      toLogin1,
      ...toRefs(state),
      search,
      onSearch,
      searchRef,
      onClear,
      updateVal,
      actions,
      onSelect,
      showPopover,
    };
  },
};
</script>

<style scoped>
.navBar {
  padding: 15px;
  background-color: #fff;
}
.left {
  position: relative;
}
.navBar,
.left,
.right {
  display: flex;
  align-items: center;
}
.search .icon {
  position: relative;
}
.tips {
  width: 500px;
  position: absolute;
  background-color: #fff;
  left: 133px;
  /* padding: 0 20px; */
  display: flex;
  box-shadow: 0px 8px 18px #c8c9cc;
  border-radius: 10px;
}
.tips2 {
  width: 500px;
  position: absolute;
  background-color: #fff;
  left: 133px;
  padding: 0 20px;
  box-shadow: 0px 8px 18px #888888;
  border-radius: 10px;
}

.t-left,
.t-right {
  /* border: 1px solid; */
  width: 210px;
  padding: 10px;
}
.t-right {
  margin-left: 10px;
}
.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.clear {
  font-size: 13px;
  cursor: pointer;
}
.clear:hover {
  color: #1ecd9b;
}
.row1-line {
  background: #ebedf0;
  position: absolute;
  top: 0px;
  width: 1px;
  left: 249px;
  bottom: 0px;
}
.row2-line {
  background: #ebedf0;
  position: absolute;
  left: 0px;
  right: 0px;
  top: 42px;
}

.rank-item {
  height: 30px;
  line-height: 30px;
  white-space: nowrap;
  position: relative;
  padding: 0 20px;
}
.playNum {
  font-size: 10px;
  position: absolute;
  right: 10px;
}
.avator {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 20px;
  vertical-align: middle;
}
.avator-sm {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 20px;
  vertical-align: middle;
}
.avator img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.right {
  width: 626px;
  justify-content: end;
  cursor: pointer;
  margin-right: 20px;
}
.icon {
  margin-left: 30px;
}
.iconfont {
  cursor: pointer;
}
.keyItem {
  height: 37px;
  line-height: 37px;
  transform: translateY(-13px);
  cursor: pointer;
  border-radius: 10px;
  padding-left: 8px;
}
.keyItem:hover {
  background-color: #f6f6f6;
}
li {
  cursor: pointer;
  border-radius: 10px;
}
li:hover {
  background-color: #f6f6f6;
}
</style>