import { createStore } from 'vuex'


import createPersistedState from "vuex-persistedstate"

export default createStore({
  plugins: [createPersistedState()],
  state: {
    historyList:[],
    playList:[],
    latestList:[],
    profile:{},
    playIndex:0
  },
  mutations: {
    setProfile(state,obj){
      state.profile = obj
    },
    deleteProfiler(state){
      state.profile = {}
    },
    setPlayList(state,obj){//播放队列
      
      state.playList.unshift(obj)
    },
    deletePlayList(state){
      state.playList = []
    },
    setHistoryList(state,keyWords){//搜索历史记录
      for(let i=0;i<state.historyList.length;i++){
        if(state.historyList[i]==keyWords.trim()) return
      }
      state.historyList.push(keyWords)
    },
    deleteHistoryList(state){
      state.historyList = []
    },
    setLatestList(state,obj){
      for(let i=0;i<state.latestList.length;i++){
        if(state.latestList[i].id==obj.id){
          [state.latestList[0],state.latestList[i]] = [state.latestList[i],state.latestList[0]]
          return
        }
        
      }
      state.latestList.unshift(obj)
    },
    setPlayIndex(state,index){
      state.playIndex = index
    }
  },
  actions: {
  },
  modules: {
  }
})
