<template>
  <div class="home">
    <div class="right">
      <div class="container">
        <div class="head">音乐馆</div>
        <ul>
          <li
            class=""
            v-for="(item, index) in list"
            :key="item.index"
            @click="childSwitch(index)"
            :class="actIndex == index ? 'active' : ''"
          >
            {{ item }}
          </li>
        </ul>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import { useRouter } from "vue-router";
import { reactive, toRefs, onMounted, watch } from "vue";
// import { ref } from 'vue';
export default {
  setup() {
    const router = useRouter();
    const state = reactive({
      actIndex: 0,
    });
    const list = ["精选", "排行", "歌手", "分类歌单", "数字专辑", "手机专享"];
    const children = [
      "/selected",
      "/rank",
      "/singer",
      "/category",
      "/cd",
      "/phone",
    ];
    const childSwitch = (e) => {
      state.actIndex = e;
      // console.log(router.currentRoute.value.meta.index);
      router.push({ path: `/home${children[e]}` });
    };
    watch(()=>router.currentRoute.value.meta.index,(newVal)=>{
      state.actIndex = newVal
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    })
    
    onMounted(() => {
  
      router.push({ path: "/home/selected" });
      
    });
    return {
      children,
      ...toRefs(state),
      list,
      childSwitch,
    };
  },
};
</script>

