<template>
  <div class="player-wrapper" @mousemove="onMove1" v-if="playList.length">
    <van-divider
      class="divider"
      :style="{ color: '#1989fa', borderColor: '#969799', marginTop: '0' }"
      @click="forward"
      ><div class="circle" :style="circleStyle" id="process"></div>
    </van-divider>
    <div class="process-line" :style="progressStyle" @click="forward"></div>
    <!-- <input type="range" id="slider" /> -->
    <div class="flex-wrap">
      <div class="img" v-if="picUrl">
        <img :src="picUrl" />
      </div>
      <div class="img" v-else>
        <img
          src="https://y.qq.com/music/photo_new/T002R300x300M000002hkEW33jS7mF_1.jpg?max_age=2592000"
          alt=""
        />
      </div>
      <div class="desc">
        <div class="top">
          <span class="songName">{{ name }}</span>
          <i class="iconfont icon-mv"></i>
        </div>
        <div class="bottom">
          <span class="author">{{ al }}</span>
        </div>
        <div class="operator">
          <i class="bi bi-heart" v-if="!isLike" @click="onLike"></i>
          <i class="bi bi-heart-fill" v-else @click="onLike"></i>
          <i class="bi bi-download "></i>
          <i class="bi bi-three-dots"></i>
          <i class="bi bi-chat-square-dots "></i>
        </div>
      </div>
      <div class="progressBar">
        <van-popover
          v-model:show="showPopover1"
          placement="top"
          class="wayPoper"
        >
          <ul class="playWay">
            <li @click="onSelect1('icon-suijibofang')">
              <i class="iconfont icon-suijibofang" style="font-size: 18px"></i>
              随机播放
            </li>
            <li @click="onSelect1('icon-shunxubofang')">
              <i class="iconfont icon-shunxubofang" style="font-size: 18px"></i>
              顺序播放
            </li>

            <li @click="onSelect1('icon-danquxunhuan')">
              <i class="iconfont icon-danquxunhuan" style="font-size: 18px"></i>
              单曲循环
            </li>
            <li @click="onSelect1('icon-liebiaoxunhuan')">
              <i
                class="iconfont icon-liebiaoxunhuan"
                style="font-size: 18px"
              ></i>
              列表循环
            </li>
          </ul>
          <template #reference>
            <i
              class="iconfont"
              :class="playWay"
              style="font-size: 18px; margin-left: 6px"
            ></i>
          </template>
        </van-popover>
    
        <i class="bi bi-skip-start-fill biHover " @click="switchSong(playIndex - 1)"></i>
        <i class="bi bi-play-circle-fill" @click="startPlay" v-if="!isPlay"></i>
        <i class="bi bi-pause-circle-fill" v-else @click="startPlay"></i>
        <i class="bi bi-skip-end-fill biHover"  @click="switchSong(playIndex + 1)"></i>
        <i class="bi bi-volume-down biHover" @click="switchSound"
          v-if="value > 0"></i>
        <i class="bi bi-volume-mute biHover"  v-else
          @click="switchSound"></i>
        
  
        <div class="slide-wrapper" v-if="showMute">
          <van-slider
            v-model="value"
            @change="onChange"
            vertical
            class="slide"
          />
          <span>{{ value }}%</span>
        </div>
        <span class="time">{{
          String(Math.floor(startTime / 60)).padStart(2, "0") +
          ":" +
          String(Math.floor(startTime % 60)).padStart(2, "0") +
          "/" +
          String(Math.floor(endTime / 60)).padStart(2, "0") +
          ":" +
          String(Math.floor(endTime % 60)).padStart(2, "0")
        }}</span>
        <div class="setting">
          <!-- <span @click="handleSpeed">倍速</span> -->
          <van-popover
            v-model:show="showSpeed"
            placement="top"
            :actions="actions1"
            @select="speedSelect"
          >
            <template #reference>
              <span>{{ speed == 1 ? "" : speed }}倍速</span>
            </template>
          </van-popover>
          <van-popover
            v-model:show="showPopover2"
            placement="top"
            :actions="actions2"
            @select="onSelect2"
          >
            <template #reference>
              <span class="quality">{{ standard.slice(0, 2) }}</span>
            </template>
          </van-popover>
          <span>音效</span>
        </div>
        <span class="endIcon">
          <i class="iconfont icon-ci" @click="getLyric"></i>
          <van-popover v-model:show="showSingList" placement="top">
            <div class="listHead">
              <p class="p1">播放列表</p>
              <div class="p2">
                <p class="p2-title">{{ count }}首歌曲</p>
                <div class="set-icon">
                  <van-icon name="orders-o" />
                  <van-icon name="delete-o" @click="deleteList" />
                </div>
              </div>
            </div>
            <ul class="listContent">
              <li
                v-for="(item, index) in playList"
                :key="item.id"
                class="listItem"
                @click="switchSong(index)"
                :class="playIndex == index ? 'active' : ''"
              >
                <div class="itemHead">
                  <span class="songName">{{ item.songName }}</span>
                  <span class="addition">VIP</span>
                  <span class="addition">SQ</span>
                  <i class="iconfont icon-mv"></i>
                </div>
                <div class="author-wrapper">
                  {{ item.author }}
                </div>
              </li>
            </ul>
            <template #reference>
              <i class="bi bi-music-note-list biHover"></i>{{ count }}
            </template>
          </van-popover>
        </span>
      </div>
    </div>
    <audio :src="songUrl" ref="audioRef"></audio>
  </div>
  <div class="player-wrapper" v-else>
    <van-divider
      class="divider"
      :style="{ color: '#1989fa', borderColor: '#969799', marginTop: '0' }"
    />
    <div class="flex-wrap">
      <div class="img">
        <img
          src="https://y.qq.com/music/photo_new/T002R300x300M000002hkEW33jS7mF_1.jpg?max_age=2592000"
          alt=""
        />
      </div>
      <div class="desc">
        <div class="top">
          <span class="songName">QQ音乐</span>
          <i class="iconfont icon-mv"></i>
        </div>
        <div class="bottom">
          <span class="author">听你想听</span>
        </div>
        <div class="operator">
          <i class="bi bi-heart" ></i>
          <i class="bi bi-download "></i>
          <i class="bi bi-three-dots"></i>
          <i class="bi bi-chat-square-dots "></i>
        </div>
      </div>
      <div class="progressBar">
        <van-popover
          v-model:show="showPopover1"
          placement="top"
          class="wayPoper"
        >
          <ul class="playWay">
            <li @click="onSelect1('icon-suijibofang')">
              <i class="iconfont icon-suijibofang" style="font-size: 18px"></i>
              随机播放
            </li>
            <li @click="onSelect1('icon-shunxubofang')">
              <i class="iconfont icon-shunxubofang" style="font-size: 18px"></i>
              顺序播放
            </li>

            <li @click="onSelect1('icon-danquxunhuan')">
              <i class="iconfont icon-danquxunhuan" style="font-size: 18px"></i>
              单曲循环
            </li>
            <li @click="onSelect1('icon-liebiaoxunhuan')">
              <i
                class="iconfont icon-liebiaoxunhuan"
                style="font-size: 18px"
              ></i>
              列表循环
            </li>
          </ul>
          <template #reference>
            <i
              class="iconfont"
              :class="playWay"
              style="font-size: 18px; margin-left: 6px"
            ></i>
          </template>
        </van-popover>

        <i class="bi bi-skip-start-fill biHover " ></i>
        <i class="bi bi-play-circle-fill" @click="onToast" ></i>
        <i class="bi bi-skip-end-fill biHover"  @click="switchSong(playIndex + 1)"></i>
        <i class="bi bi-volume-down biHover" ></i>
        

        <div class="slide-wrapper" v-if="showMute">
          <van-slider
            v-model="value"
            @change="onChange"
            vertical
            class="slide"
          />
          <span>{{ value }}%</span>
        </div>
        <span class="time">{{
          String(Math.floor(0)).padStart(2, "0") +
          ":" +
          String(Math.floor(0)).padStart(2, "0") +
          "/" +
          String(Math.floor(0)).padStart(2, "0") +
          ":" +
          String(Math.floor(0)).padStart(2, "0")
        }}</span>
        <div class="setting">
          <van-popover
            v-model:show="showSpeed"
            placement="top"
            :actions="actions1"
            @select="speedSelect"
          >
            <template #reference>
              <span>{{ speed == 1 ? "" : speed }}倍速</span>
            </template>
          </van-popover>
          <van-popover
            v-model:show="showPopover2"
            placement="top"
            :actions="actions2"
            @select="onSelect2"
          >
            <template #reference>
              <span class="quality">{{ standard.slice(0, 2) }}</span>
            </template>
          </van-popover>
          <span>音效</span>
        </div>
        <span class="endIcon">
          <i class="iconfont icon-ci"></i>
          <van-popover v-model:show="showSingList" placement="top">
            <div class="listHead">
              <p class="p1">播放列表</p>
              <div class="p2">
                <p class="p2-title">0首歌曲</p>
                <div class="set-icon">
                  <van-icon name="orders-o" />
                  <van-icon name="delete-o" @click="deleteList" />
                </div>
              </div>
            </div>
            <ul class="listContent"></ul>
            <template #reference>
              <i class="iconfont icon-bofangduilie"></i>0
            </template>
          </van-popover>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, reactive, toRefs, ref, watch } from "vue";
import { useStore } from "vuex";
import { Toast } from "vant";
// import axios from 'axios';
export default {
  setup() {
    const state = reactive({
      isPlay: false,
      showMute: false,
      lyric: "",
      playWay: "icon-liebiaoxunhuan",
      standard: "标准音质",
      speed: 1,
      showPopover1: false,
      showSingList: false,
      showSpeed: false,
      isMove: false,
      playList: [],
      playIndex: 0,
      startTime: 0,
      endTime: 190,
      isLike:false
    });
    const store = useStore();
    const switchSound = () => {
      state.showMute = !state.showMute;
    };
    const onLike = ()=>{
      state.isLike = !state.isLike
    }
    const onToast = () => {
      Toast("列表中还没有音乐");
    };
    const deleteList = () => {
      store.commit("deletePlayList");
      state.playList = [];
    };
    const switchSong = (index) => {
      if (index < 0) index = state.playList.length - 1;
      else if (index >= state.playList.length) index = 0;
      state.playIndex = index;
      forcePlay();
    };
    watch(
      () => store.state.playList,
      (newVal) => {
        console.log("加入了新歌");
        state.playList = newVal;
      },
      { deep: true }
    );
    watch(
      () => store.state.playIndex,
      (newVal) => {
        console.log("准备播放第" + (newVal + 1) + "首歌");
        state.playIndex = newVal;
      },
      { deep: true }
    );
    const forward = (e) => {
      
      let per = e.offsetX / 1028;
      audioRef.value.pause();
      console.log(per * audioRef.value.duration);
      audioRef.value.currentTime = (per * audioRef.value.duration);
      state.isPlay = false;
      state.startTime =
        Math.floor(audioRef.value.currentTime) > state.endTime
          ? state.endTime
          : Math.floor(audioRef.value.currentTime);

      // console.log(Math.floor(per * audioRef.value.duration));
    };
    const value = ref(100);
    const value2 = ref([10, 50]);
    const onChange = (value) => {
      audioRef.value.volume = (value / 100).toFixed(1);
    };
    const startMove = (e) => {
      state.isMove = true;
      console.log("我是鼠标按下时的位置：", e.offsetX);
    };
    const finishMove = (e) => {
      state.isMove = false;
      console.log("我是鼠标松开时的位置：", e.offsetX);
    };
    const onMove = (e) => {
      if (state.isMove) {
        // forward(e)
        console.log(
          "我是鼠标移动时的位置：",
          e.offsetX,
          "我的状态:",
          state.isMove
        );
      }
    };
    const showPopover2 = ref(false);
    const speedSelect = (e) => {
      console.log(audioRef.value);
      state.speed = e.speed;
      audioRef.value.playbackRate = e.speed;
    };
    const actions1 = [
      { text: "0.5倍速", speed: 0.5 },
      { text: "1倍速", speed: 1 },
      { text: "2倍速", speed: 2 },
    ];
    const actions2 = [
      { text: "标准音质" },
      { text: "较高音质" },
      { text: "极高音质" },
      { text: "无损音质" },
    ];
    const onSelect1 = (action) => {
      state.playWay = action;
      state.showPopover1 = false;
    };
    const onSelect2 = (action) => {
      state.standard = action.text;
      state.showPopover2 = false;
    };
    const audioRef = ref(null);
    const forcePlay = () => {
      //歌单页面调用，直接转为播放状态

      state.isPlay = false;
      setTimeout(() => {
        startPlay();
      }, 500);
    };
    const startPlay = async() => {
      setTimeout(() => {
        state.endTime = Math.floor(audioRef.value.duration);
        getTime();
        state.isPlay = !state.isPlay;
        // state.startTime =
        //   Math.floor(audioRef.value.currentTime) > state.endTime
        //     ? state.endTime
        //     : Math.floor(audioRef.value.currentTime);
        store.commit("setLatestList", state.playList[state.playIndex]);
        if (state.isPlay) {
          audioRef.value.play();
        } else {
          audioRef.value.pause();
        }
      }, 0);
    };

    const getLyric = () => {
      // axios({
      //   url:`/api/getLyric?songmid=${state.playList[state.playIndex].songmid}&?isFormat=true`
      // })
    };
    const closure = () => {
      var timer = null;

      return function () {
        if (!audioRef.value||!audioRef.value.currentTime) clearInterval(timer);
        timer = setInterval(() => {
          if (state.playList.length && state.isPlay) {
            state.startTime = Math.floor(audioRef.value.currentTime);
            state.endTime = Math.floor(audioRef.value.duration);
            if(state.startTime==state.endTime){//当歌曲播放完
              if(state.playWay=='icon-liebiaoxunhuan'){//列表循环

                state.playIndex ++
                if(state.playIndex==state.playList.length){
                  state.playIndex = 0
                }
              }else if(state.playWay=='icon-shunxubofang'){
                 state.playIndex ++
                if(state.playIndex==state.playList.length){
                  state.playIndex = 0
                }
              }else if(state.playWay=='icon-suijibofang'){
                state.playIndex = Math.floor(Math.random()*state.playList.length)
              }else{
                state.currentTime = 0 
              }
              forcePlay()
            }
          }
        }, 1000);
      };
    };
    const getTime = closure();
    onMounted(() => {
      console.log(store.state.playList);
      state.playList = store.state.playList;
    });
    return {
      switchSong,
      closure,
      startMove,
      finishMove,
      deleteList,
      onMove,
      startPlay,
      getTime,
      process,
      ...toRefs(state),
      audioRef,
      getLyric,
      forcePlay,
      showPopover2,
      forward,
      actions1,
      actions2,
      onSelect1,
      onSelect2,
      value,
      value2,
      speedSelect,
      onChange,
      switchSound,
      onToast,
      onLike
    };
  },
  computed: {
    progressStyle() {
      let persent = Math.ceil((this.startTime / this.endTime) * 1044);
      return `width:${persent}px;`;
    },
    circleStyle() {
      let persent = Math.ceil((this.startTime / this.endTime) * 1038);
      return `left:${persent}px;`;
    },
    songUrl() {
      return this.playList[this.playIndex].songUrl;
    },
    name() {
      return this.playList[this.playIndex].songName;
    },
    al() {
      return this.playList[this.playIndex].author;
    },
    picUrl() {
      return this.playList[this.playIndex].coverPic;
    },
    id() {
      return this.playList[this.playIndex].id;
    },
    // endTime() {
    //   return this.playList[this.playIndex].interval;
    // },
    count() {
      return this.playList.length;
    },
  },
};
</script>

<style lang="less" scoped>
.player-wrapper {
  user-select: none;
  height: 105px;
  width: 1044px;
  background-color: #fff;
  position: relative;
  .divider {
    color: #969799;
    border-color: #969799;
    margin-top: 0px;
    border: 1px solid #969799;
    .circle {
      cursor: pointer;
      display: none;
      height: 10px;
      width: 10px;
      border-radius: 50%;
      background-color: #1ed2a9;
      position: absolute;
      top: -3px;
      left: 0;
    }
  }
  .process-line {
    width: 0;
    height: 4px;
    background-color: #1ed2a9;
    position: absolute;
    top: 0;
  }

  .flex-wrap {
    display: flex;
    padding: 0 20px;
    .img {
      cursor: pointer;
      width: 60px;
      height: 60px;
      img {
        width: 60px;
        border-radius: 10px;
      }
      img:hover {
        filter: brightness(0.7);
      }
    }
    .desc {
      width: 250px;
      margin: 0 8px;
      font-size: 13px;
      cursor: pointer;
      .top {
        white-space: nowrap;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        cursor: pointer;
        .songName {
          font-size: 13px;
        }
        i {
          vertical-align: middle;
        }
      }
      .bottom {
        margin-top: 5px;
        font-size: 11px;
        color: #000000b3;
      }
      .operator {
        margin-top: 5px;
        .bi-heart:hover{
          color: red;
        }
        .bi-heart-fill{
          color: red;
        }
        .bi-heart-fill:hover{
          color: red;
        }
        i {
          cursor: pointer;
          margin-right: 20px;
          font-size: 14px;
        }
        i:hover {
          color: #1ed2a8;
        }
      }
    }
    .progressBar {
      width: 700px;
      padding: 20px;
      margin-top: -9px;
      position: relative;
      .slide-wrapper {
        text-align: center;
        position: absolute;
        left: 227px;
        bottom: 95px;
        height: 232px;
        width: 80px;
        border-radius: 10px;
        z-index: 100;
        background: #fff;
        border: 1px solid #e5e5e5;
        .slide {
          margin: 10px 40px;
          height: 183px;
        }
      }
      i {
        font-size: 28px;
        margin-left: 28px;
        vertical-align: middle;
        cursor: pointer;
      }
      .biHover:hover {
        color: #1ed2a8;
      }
      .bi-play-circle-fill,
      .bi-pause-circle-fill {
        color: #1ed2a8;
        font-size: 38px;

      }
      .icon-shengyin,
      .icon-shengyinjingyin {
        margin-right: 100px;
      }
      .time{
        position: absolute;
        right: 217px;
        bottom: 32px;
      }
      .setting {
        position: absolute;
        right: 217px;
        bottom: 32px;
        display: none;
        span {
          margin-left: 10px;
          font-size: 11px;
          border: 1px solid #d5d5d5;
          // padding: 3px;
        }
        span:hover {
          color: #1ecc94;
        }
      }
      .endIcon {
        position: absolute;
        right: 107px;
        bottom: 32px;
        cursor: pointer;
        i {
          font-size: 18px;
        }
      }
    }
    .progressBar:hover {
      .time {
        visibility: hidden;
      }
      .setting {
        display: block;
      }
    }
  }
}
.player-wrapper:hover {
  .circle {
    display: block;
  }
}
.playWay {
  li {
    color: #323233;
    width: 138px;
    text-align: center;
    height: 38px;
    line-height: 38px;
    font-size: 14px;
    vertical-align: middle;
    border-bottom: 1px solid #f2f3f5;
    cursor: pointer;
  }
  .van-divider {
    // display: none;
    // margin-top: -5px;
  }
}
.listHead {
  width: 300px;
  .p1 {
    font-size: 24px;
    font-weight: bold;
    padding: 0 20px;
  }
  .p2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .p2-title {
      margin: 0 20px;
      font-size: 13px;
      color: #646566;
    }
    .set-icon {
      margin-right: 22px;
      font-size: 20px;
      i {
        cursor: pointer;
        margin-right: 8px;
      }
      i:hover {
        color: #1ecd9b;
      }
    }
  }
}
.listContent {
  width: 300px;
  height: 442px;
  overflow: scroll;
  .listItem {
    padding: 6px 20px;
    cursor: pointer;
    .itemHead {
      font-size: 12px;
      .songName {
        font-size: 16px;
        color: #323233;
        font-weight: bold;
        vertical-align: middle;
      }
      .addition {
        border: 1px solid #1ecd9b;
        color: #1ecd9b;
        position: relative;
        bottom: -2px;
        margin-left: 8px;
      }
      .icon-mv {
        margin-left: 8px;
        position: relative;
        bottom: -4px;
      }
    }
    .author-wrapper {
      font-size: 13px;
    }
  }
  .listItem:hover {
    background-color: #f7f8fa;
  }
  .active {
    background-color: #f7f8fa;
  }
}
</style>