<template>
  <div class="leftBar">
    <div class="logo">
      <i class="iconfont icon-QQyinle"></i>
      <span>QQ音乐</span>
    </div>
    <div class="online-music">
      <p>在线音乐</p>
      <!-- <div class="li-item" :class="index==0? 'active':''" @click="goto(0)">
        <van-icon name="award" class="icon"/>
        <span>推荐</span>
      </div> -->
      <div class="li-item" :class="index==1? 'active':''" @click="goto(1)">
        <i class="iconfont icon-yinle"></i>
        <span>音乐馆</span>
      </div>
      <div class="li-item" :class="index==2? 'active':''" @click="goto(2)">
        <i class="iconfont icon-shipin"></i>
        <span>视频</span>
      </div>
      <div class="li-item" :class="index==3? 'active':''" @click="goto(3)">
        <i class="iconfont icon-diantai"></i>
        <span>电台</span>
      </div>
    </div>
     <div class="local-music">
      <p>我的音乐</p>
      <div class="li-item" :class="index==4? 'active':''" @click="goto(4)">
        <i class="iconfont icon-diannao"></i>
        <span>本地和下载</span>
      </div>
      <div class="li-item" :class="index==5? 'active':''" @click="goto(5)">
        <i class="iconfont icon-zuijinbofang
"></i>
        <span>最近播放</span>
      </div>
      <div class="li-item" :class="index==6? 'active':''" @click="goto(6)">
        <i class="iconfont icon-shitingliebiao
"></i>
        <span>试听列表</span>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs,watch} from 'vue'
import {useRouter} from 'vue-router'
export default {
  setup(){
    const state = reactive({
      index:-1
    })
    const router = useRouter()
    const list = ['/recommend','/home','/video','/radio','/local','/latest','/audition']
    const goto = (e)=>{
      router.push({path:`${list[e]}`})
    }

     watch(() =>router.currentRoute.value.path,(newValue)=> {
        let bodySrcollTop = document.body.scrollTop;
        if (bodySrcollTop !== 0) {
          document.body.scrollTop = 0;
          return;
        }
        let docSrcollTop = document.documentElement.scrollTop;
        if (docSrcollTop !== 0) {
          document.documentElement.scrollTop = 0;
        }
        let path = newValue
        for(let i=0;i<list.length;i++){
          if(list[i].includes(path)){
            state.index=i
            // console.log(state.index);
            return
          }
        }
        // state.index = 1
      },{ immediate: true })
    return {
      goto,
      list,
      ...toRefs(state)
    }
  }
 

}
</script>

<style scoped>
.leftBar{
  width: 165px;
  height: 900px;
  background-color:#F6F6F6;
  padding: 25px  35px;
}
.iconfont,.icon{
  font-size: 30px !important;
  vertical-align: middle;
}
.logo,.online-music,.local-music{
  
  
}
.li-item{
  margin: 20px 0;
  padding: 5px;
  border-radius: 5px;
  vertical-align: -5px;
  cursor: pointer;
}
span{
  display: inline-block;
  margin: 8px;
}
.active{
  background-color: #1ECD9B;
  color: #fff !important;
}
.active .icon{

}
.li-item:not(.active):hover{
  background-color: #E4E4E4;
}
.online-music{
 margin-top: 30px;
}
.local-music{
  margin-top: 30px;
}
</style>