import {
  createRouter,
  createWebHistory
} from 'vue-router'
import Home from '../views/Home.vue'

const routes = [{
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'Home',
    component: Home,
    meta: {
      RtIndex: 0
    },
    children: [{
        path: 'selected',
        component: () => import('../views/selected.vue'),
        meta: {
          index: 0
        }
      },
      {
        path: 'rank',
        component: () => import('../views/rank.vue'),
        meta: {
          index: 1
        }
      },
      {
        path: 'singer',
        component: () => import('../views/singer.vue'),
        meta: {
          index: 2
        }
      },
      {
        path: 'category',
        component: () => import('../views/category.vue'),
        meta: {
          index: 3
        }
      },
      {
        path: 'cd',
        component: () => import('../views/cd.vue'),
        meta: {
          index: 4
        }
      },
      {
        path: 'phone',
        component: () => import('../views/phone.vue'),
        meta: {
          index: 5
        }
      },
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue'),
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('../views/Register.vue'),

  },
  {
    path: '/video',
    name: 'Video',
    component: () => import('../views/Video.vue'),
    meta: {
      RtIndex: 1
    },
  },
  {
    path: '/radio',
    name: 'Radio',
    meta: {
      RtIndex: 2
    },
    component: () => import('../views/Radio.vue')
  },
  {
    path: '/local',
    name: 'Local',
    meta: {
      RtIndex: 3
    },
    component: () => import('../views/Local.vue')
  },
  {
    path: '/latest',
    name: 'Latest',
    meta: {
      RtIndex: 4
    },
    component: () => import('../views/Latest.vue')
  },
  {
    path: '/audition',
    name: 'Audition',
    meta: {
      RtIndex: 5
    },
    component: () => import('../views/Audition.vue')
  },
  {
    path: '/playList',
    name: 'PlayList',
    component: () => import('../views/PlayList.vue')
  },
  {
    path: '/rankDetail',
    name: 'rankDetail',
    component: () => import('../views/rankDetail.vue')
  },
  {
    path: '/searchPage',
    name: 'searchPage',
    component: () => import('../views/searchPage.vue')
  },
  {
    path: '/recommend',
    name: 'recommend',
    component: () => import('../views/recommend.vue')
  },
  {
    path: '/singerDeatil',
    name: 'singerDeatil',
    component: () => import('../views/singerDeatil.vue')
  },
  {
    path: '/cdListPage',
    name: 'cdListPage',
    component: () => import('../views/cdListPage.vue')
  },
  {
    path: '/videoPage',
    name: 'videoPage',
    component: () => import('@/views/videoPage')
  },
  
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior:()=>{
    return {x:0,y:0}
  }
})
router.afterEach(() => {

  let bodySrcollTop = document.body.scrollTop
  if (bodySrcollTop !== 0) {
    document.body.scrollTop = 0
    return
  }
  let docSrcollTop = document.documentElement.scrollTop
  if (docSrcollTop !== 0) {
    document.documentElement.scrollTop = 0
  }
})
export default router