import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 引入模块后自动生效
import '@vant/touch-emulator';
import './assets/fonts/iconfont.css'
import { Search,Form, Field, NoticeBar,CellGroup,Empty,Slider,Button,Icon,Popup,Swipe,Popover,Overlay,SwipeItem,CountDown,Divider,List,Collapse,CollapseItem} from 'vant';
const app = createApp(App)
app.use(Search);
app.use(NoticeBar);
app.use(Form);
app.use(Field);
app.use(CellGroup);
app.use(Button);
app.use(Icon);
app.use(Swipe);
app.use(Empty);
app.use(SwipeItem);
app.use(CountDown);
app.use(Divider);
app.use(List);
app.use(Collapse);
app.use(CollapseItem);
app.use(Popover);
app.use(Overlay);
app.use(Slider);
app.use(Popup);
app.use(store).use(router).mount('#app')

